.news {
    .content {
        width: 1000px;
        margin: 0 auto;
        padding-top: 100px;
        .title {
            font-size: 25px;
            font-weight: bold;
            margin-bottom: 50px;
        }
        .box {
            margin-top: 50px;
            .n-item {
                border-bottom: 1px solid #eee;
                padding: 30px 0;
                .img {
                    width: 180px;
                    height: 80px;
                    margin-right: 50px;
                }
                .brad {
                    background-color: #1868b7;
                    color: #fff;
                    line-height: 3;
                    padding: 0 5px;
                    border-radius: 5px;
                    font-size: 14px;
                }
                .title {
                    font-size: 16px;
                    color: #333;
                    margin-bottom: 15px;
                }
                .time {
                    font-size: 14px;
                    color: #666;
                }
            }
        }
    }
    
}