.header {
    padding: 10px 30px;
    border-bottom: 1px solid rgb(229, 232, 235);
    box-shadow: rgba(14,14,14,0.25) 0px 0px 8px 0px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    background-color: #fff;
    right: 0;
    .logo {
        width: 140px;
        height: auto;
        margin-right: 80px;
    }
    .l-menu {
        font-size: 16px;
        font-weight: 500;
        color: rgba(14, 14, 14, 0.75);
        .lm-item {
            padding: 15px 0;
            margin: 0 15px;
            cursor: pointer;
            border-bottom: 3px solid transparent;
            &:hover, &.active {
                color: rgba(14, 14, 14, 0.95);
                border-color: #16a085;
            }
        }
    }
}